
import basepage from "~/mixins.js/basepage.js";
import {mapActions, mapGetters} from 'vuex'

export default {
  name: 'welcome',
  mixins: [basepage],
  transition: {
    name: 'fade'
  },
  beforeMount() {

    console.log('sono la index opening');
    this.$analytics.setScreenName("WelcomeSDKPage");
  },
  mounted() {
    console.log('ready to load airlyn features');
    if (window.currentVMToken) {
      console.log('Current VM Token', window.currentVMToken)
    }
    console.log('Check environment ready:', window.currentExercise, window.currentChallenge, window.currentVMToken)
    this.$keepawakemanager();

    this.$deviceInfo.getInfo().then((r) => {
      this.deviceInfo = r;
      this.$exerciseManager.setDeviceCode(r);
    });

  }
